.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.img-pan{
  position: relative;
}
.img-pan img{
  /* max-width: 100%;
  max-height: 100%; */
}
.rect{
  position: absolute;
  border: solid #ff0000 2px;
}
.rect-text{
  position: absolute;
  color: #ffff;
  background-color: #ff0000;
  font-size: 14px;
  border: 2px solid #ff0000;
  text-align: left;
}

div[tabindex="0"]{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}